  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.boton-amarillo {
  background-color: #EDDDBD;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #e8d3a9;
  cursor: pointer;
}

.boton-amarillo-noblock {
  background-color: #EDDDBD;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
}
.boton-amarillo-noblock:hover {
  background-color: #e8d3a9;
  cursor: pointer;
}

.boton-verde {
  background-color: #0C8282;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #0a6b6b;
  cursor: pointer;
}

.header {
  background-color: #0C8282;
  height: 6rem;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.header .navegacion {
  display: flex;
  gap: 4rem;
}
.header .navegacion a {
  color: #FFFFFF;
}
.header .navegacion a:hover {
  color: #EDDDBD;
}

.barra_busqueda {
  background-color: #7EB19F;
  margin-top: 7rem;
  display: grid;
  grid-template-columns: 4fr 1fr;
}
.barra_busqueda .barra {
  display: flex;
  align-items: center;
  justify-content: center;
}
.barra_busqueda .barra input {
  height: 40%;
  width: 80%;
}

.cuadro_empleados {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 5rem;
}
.cuadro_empleados .tabla_empleados thead {
  background-color: #0C8282;
}
.cuadro_empleados .tabla_empleados th {
  padding: 2rem 1.5rem;
  font-weight: bold;
  color: #FFFFFF;
}
.cuadro_empleados .tabla_empleados td {
  padding: 1.5rem;
  border-bottom: solid 1px #e1e1e1;
}
.cuadro_empleados .tabla_empleados tbody tr {
  cursor: pointer;
}
.cuadro_empleados .tabla_empleados tbody tr:hover {
  background-color: #e1e1e1;
}

.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.login input {
  display: block;
}
.login .botones {
  margin-top: 3rem;
  display: flex;
  gap: 5rem;
}

.cuadro_empleado {
  margin-top: 6rem;
  background-color: #7EB19F;
  display: grid;
  gap: 1rem;
  grid-template-columns: 1fr 3fr;
}
.cuadro_empleado .empleado_izquierda {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.cuadro_empleado .empleado_izquierda .foto-empleado {
  height: 60%;
  width: 60%;
  border-bottom: solid 1.5rem #EDDDBD;
  margin: 3rem 0;
}
.cuadro_empleado .empleado_izquierda .botones-empleado {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  margin-bottom: 3rem;
}
.cuadro_empleado .empleado_derecha h1, .cuadro_empleado .empleado_derecha h2, .cuadro_empleado .empleado_derecha h3 {
  text-align: left;
}